@import 'components/variables';

.section-pedidos {
  padding-top: 100px;
  background-image: url(../images/team/bg-team-mobile.png);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  @media (min-width: 992px) {
    background: #efeff7;
    background-image: url(../images/team/bg-team.png);
    background-position: center top;
    background-repeat: no-repeat;
    background-size: contain;
    padding-top: 150px;
    padding-bottom: 3rem;
  }
  .content-container {
    background-color: $white;
    padding: 0;
    @media (max-width: 991.98px) {
      max-width: 100%;
    }
    @media (min-width: 576px) {
      border-radius: 25px;
    }
    @media (min-width: 992px) {
      background: $white;
      padding-bottom: 5rem;
    }
  }
  .container1 {
    background: transparent;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    padding-left: 1rem;
    padding-right: 1rem;
    @media (min-width: 992px) {
      background: $white;
    }
    @media (max-width: 991.98px) {
      p {
        color: $red;
      }
    }
  }
  .container2 {
    background: $white;
    padding-left: 1rem;
    padding-right: 1rem;
    @media (max-width: 991.98px) {
      max-width: 100%;
      padding-bottom: 4rem;
    }
    @media (min-width: 992px) {
      border-bottom-left-radius: 25px;
      border-bottom-right-radius: 25px;
      padding-bottom: 4rem;
    }
    .col-izq {
      display: none;
      border-right: 1px solid $gray-light;
      @media (min-width: 992px) {
        display: flex;
        justify-content: center;
        align-items: flex-start;
      }
      ul {
        padding-left: 0;
        list-style: none;
        li {
          &:before {
            background: $red;
          }
          &.active {
            &:before {
              left: 0;
              right: 0;
            }
          }
          .items {
            font-weight: bold;
            cursor: pointer;
            color: $black;
            text-decoration: none;
            transition: all 0.3s;
            p {
              margin-bottom: 0;
              padding: .5rem;
            }
            &.close-sesion {
              color: $red;
            }
          }
        }
      }
    }
    .col-der {
      order: 1;
      @media (min-width: 992px) {
        order: 2;
      }
      .imgprogressbar {
        position: relative;
        height: 50px;
        margin-bottom: 0.5rem;
        img {
          position: absolute;
          width: 50px;
          top: 50%;
          transform: translate(-50%, -50%);
        }
        img:nth-child(1) {
          left: 5%;
        }
        img:nth-child(2) {
          left: 50%;
        }
        img:nth-child(3) {
          left: 95%;
        }
      }
      .progressbar {
        position: relative;
        height: 20px;
        display: flex;
        align-items: center;
        .bar-base {
          position: relative;
          width: 100%;
          height: 5px;
          background: #f2f2f2;
          box-shadow: inset 0 0 5px 0 rgba($color: #000000, $alpha: 0.25);
          border-radius: 2px;
          .bar-check {
            width: 50%;
            height: 5px;
            background: linear-gradient(180deg,#e3bdbd 13%,#f7a4a4 30%,#f32d2d 67%,#f7a4a4 85%,#cd9797 100%);
            border-radius: 2px;
            div {
              position: absolute;
              top: 50%;
              transform: translate(-50%, -50%);
              width: 20px;
              height: 20px;
              background: #c8c8c8;
              border-radius: 50%;
              box-shadow: inset 0 0 5px 0 rgba($color: #000000, $alpha: 0.25);
            }
            div:nth-child(1) {
              left: 5%;
              background: radial-gradient(circle, #f7a4a4 0%, #f32d2d 62%, #f7a4a4 100%);
            }
            div:nth-child(2) {
              left: 50%;
              background: radial-gradient(circle, #f7a4a4 0%, #f32d2d 62%, #f7a4a4 100%);
            }
            div:nth-child(3) {
              left: 95%;
            }
          }
        }
      }
      .textprogressbar {
        position: relative;
        height: 30px;
        span {
          position: absolute;
          top: 50%;
          transform: translate(-50%, -50%);
          color: $gray;
          font-weight: 600;
        }
        span:nth-child(1) {
          left: 5%;
        }
        span:nth-child(2) {
          left: 50%;
        }
        span:nth-child(3) {
          left: 95%;
        }
      }
      .pedido {
        border-top: 1px solid $text-gray;
        margin-top: 2rem;
        padding-top: 1rem;
        .text-article {
          padding: .2rem;
          background-color: $gray-light;
          border-radius: 5px;
          width: 70%;
          text-align: center;
        }
        .content-img-product {
          width: 100%;
          padding-bottom: 100%;
          position: relative;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            position: absolute;
          }
        }
      }
    }
  }
}

#anadirNueva,
#editarDireccion {
  .modal-dialog {
    .modal-content {
      .modal-header {
        .btn-close {
          &:focus {
            color: #212529;
            background-color: #fff;
            border-color: $red;
            outline: 0;
            box-shadow: none;
          }
        }
      }
      .modal-body {
        .form-control {
          &:focus {
            color: #212529;
            background-color: #fff;
            border-color: $red;
            outline: 0;
            box-shadow: none;
          }
        }
      }
      .modal-footer {
        .btn {
          &:focus {
            outline: 0;
            box-shadow: 0 0 0 0.25rem rgba(253, 69, 13, 0.25);
          }
        }
      }
    }
  }
}
